@import url('https://fonts.googleapis.com/css2?family=Prompt&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Prompt', sans-serif;
  background-color: #a7deff;
}

p {
  font-size: 1.6rem;
}
h1 {
  font-size: 6rem;
}
.app {
  width: 100%;
  height: 100vh;
  position: relative;
}
.container{
  max-width: 700px;
  height: 500px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.app .top{
  width: 100%;
  margin: 1rem auto;
}
.bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
}
.search{
  padding: 1rem;
  text-align: center;
}
input{
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid #ffffff;
  background: #ffffff;
}

::placeholder{
  color: dimgrey;
}

.feels, .humidity{
  background-color: #fff;
  border-radius: 15px;
  padding: 1rem;
  margin: 0.5rem;
}